// src/ai/genkit.ts
import { genkit } from 'genkit';
import { googleAI } from '@genkit-ai/googleai';
import { genkitEval } from '@genkit-ai/evaluator';

console.log('[Oraculo IA] Configurando o ambiente Genkit com a nova API...');

// Exporta a instância configurada para ser usada em outros módulos.
// Este é o padrão correto para a API moderna do Genkit.
export const ai = genkit({
  plugins: [
    googleAI(),
    genkitEval(),
  ],
  traceStore: {
    provider: 'dev',
  },
  logLevel: 'debug',
  enableTracingAndMetrics: true,
});
